  height: 2px;
  left: 50%;
  position: absolute;
  top: 50%;
  transform-style: preserve-3d;
  width: 2px;
}
.stars:before, .stars:after {
  box-shadow: inherit;
  content: "";
  height: inherit;
  position: absolute;
  width: inherit;
}
.stars:before {
  animation: stars-fade1 3s linear infinite;
  transform: translateZ(-300px);
}
.stars:after {
  animation: stars-fade2 3s linear infinite;
  transform: translateZ(-600px);
}

@keyframes stars-fly {
  from {
    transform: translateZ(0px);
  }
  to {
    transform: translateZ(300px);
  }
}
@keyframes stars-fade1 {
  from {
    opacity: 0.5;
  }
  to {
    opacity: 1;
  }
}
@keyframes stars-fade2 {
  from {
    opacity: 0;
  }
  to {
    opacity: 0.5;
  }
}
